* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background-color: #292524;
  color: oklch(98.5% 0.001 106.423);
  font-family: "sono", sans-serif;
  padding: 48px 64px 0px;
  height: 80vh;
}

h1 {
  font-size: 42px;
  font-family: "Coiny", sans-serif;
  text-transform: uppercase;
  line-height: 1;
  margin-top: 6px;
}
#root {
  max-width: 1500px;
  margin: 0 auto;
}

.header {
  margin-bottom: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo {
  display: flex;
  align-items: center;
  gap: 16px;
}
.logo img {
  height: 68px;
  width: 68px;
}
/*Das ist ein Kommentar*/
.main {
  display: grid;
  grid-template-columns: 250px 1fr;
  gap: 48px;
  height: calc(100vh - 48px - 68px - 40px);
}
section {
  overflow: scroll;
  padding-bottom: 40px;
  scrollbar-color: hotpink #292524;
  scrollbar-width: thin;
}
.fact-form {
  background-color: oklch(37.4% 0.01 67.558);
  margin-bottom: 40px;
  padding: 16px 40px;
  display: flex;
  align-items: center;
  gap: 16px;
  justify-content: center;
  border-radius: 16px;
}
.hidden {
  display: none !important;
}
.message {
  text-align: center;
  text-transform: uppercase;
  /*margin-top: 32px;*/
  margin-bottom: 32px;
  font-size: 32px;
}
.disputed {
  background-color: #ef4444;
  color: white;
  padding-left: 8px;
  padding-right: 8px;
  border-radius: 100px;
  font-weight: 600;
  margin-right: 10px;
}
.fact-form input,
.fact-form select,
.fact-form textarea {
  width: 250px;
  height: 55px;
  background-color: #a8a29e;
  border: none;
  border-radius: 100px;
  padding: 16px;
  font-size: 18px;
  color: inherit;
  font-family: inherit;
  outline: none;
  resize: none;
}
.fact-form input::placeholder,
.fact-form textarea::placeholder {
  color: oklch(55.3% 0.013 58.071);
}
.fact-form textarea {
  vertical-align: middle;
  flex-grow: 1;
}
.fact-form span {
  /*font-weight: 600;*/
  font-size: 18px;
}
.fact {
  display: flex;
  align-items: center;
  gap: 24px;
  font-size: 20px;
  line-height: 1.4;
  background-color: oklch(37.4% 0.01 67.558);
  margin-bottom: 16px;
  padding: 16px 24px;
  letter-spacing: -1px;
  border-radius: 16px;
}

@media (max-width: 1200px) {
  .fact {
    flex-direction: column;
    gap: 12px;
    align-items: flex-end;
  }
}

.source:link,
.source:visited {
  color: #a8a29e;
  text-decoration: none;
  margin-left: 12px;
  transition: 300ms;
}

.source:hover,
.source:active {
  color: #3b82f6;
}

.tag {
  font-family: "Coiny", sans-serif;
  text-transform: uppercase;
  font-size: 14px;
  padding: 3px 10px 0px 10px;
  border-radius: 100px;
}

ul {
  list-style: none;
}
.vote-buttons {
  margin-left: auto;
  flex-shrink: 0;
  display: flex;
  gap: 8px;
}
.vote-buttons button {
  border: none;
  background-color: oklch(55.3% 0.013 58.071);
  font-size: 18px;
  padding: 6px 12px;
  border-radius: 100px;
  color: inherit;
  font-family: inherit;
  font-weight: 600;
  cursor: pointer;
  transition: 300ms;
}
.vote-buttons button:hover {
  background-color: #292524;
}
.vote-buttons button:disabled {
  background-color: oklch(37.4% 0.01 67.558);
}

.btn {
  border: none;
  color: inherit;
  font-family: "Coiny", sans-serif;
  line-height: 1;
  text-transform: uppercase;
  font-size: 17px;
  padding: 16px 0px 13px;
  background-image: linear-gradient(135deg, #3b82f6, #ef4444, #16a34a, #eab308);
  transition: 300ms;
  border-radius: 100px;
  cursor: pointer;
}
.btn:hover {
  transform: scale(110%) rotate(-2deg);
}
.btn-large {
  font-size: 20px;
  padding: 20px 32px 17px;
}
.btn-all-categories {
  margin-bottom: 16px;
  width: 100%;
}
.btn-category {
  width: 100%;
  background-image: none;
}
.category {
  margin-bottom: 16px;
}

@media (max-width: 1200px) {
  body {
    padding: 42px 48px;
  }
  .fact-form {
    flex-direction: column;
    align-items: stretch;
  }
  .fact-form input,
  .fact-form select {
    width: auto;
  }
  .fact-form span {
    text-align: right;
  }
  .fact-form textarea {
    height: 200px;
    border-radius: 40px;
    width: 100%;
    vertical-align: top;
  }
}
@media (max-width: 900px) {
  .main {
    grid-template-columns: 1fr;
  }
}
@media (max-width: 900px) {
  .main {
    grid-template-columns: 1fr;
    height: auto;
  }
}

@media (max-width: 600px) {
  #root {
    padding: 12px;
  }
  body {
    padding: 16px 16px;
  }
  .header {
    flex-direction: column;
    gap: 16px;
  }

  .fact-form {
    padding: 16px;
  }

  .fact {
    padding: 12px 16px;
  }
}
